// =========================== NN =========================================
// ========================================================================
/**
 * 
 * 1. inputTrainRaw( inp ),
 * 2. inputCS2MatXOneHotY()
 * 3. inputClean(),
 * 4. featureEngineering(),
 * 5. featureScaling( 1 as default | 2 ),
 *
 * 6. modelTrainCV(),
 * 7. modelTest(),
 * 8. visulization()
 */
function NN() {}

for (let i in Common.prototype) {
  NN.prototype[i] = Common.prototype[i];
}

for (let j in Optimizer.prototype) {
  NN.prototype[j] = Optimizer.prototype[j];
}

NN.prototype.softmax = function(inpZ) {
  var cols = inpZ.size()[1];
  // Zmaxexp
  var maxCurRow = math.max(inpZ, 1);
  var maxCurRow_reshape = math.reshape(maxCurRow, [maxCurRow.size()[0], 1]);
  var maxCurRow_reshape_clone = this.extendArr(maxCurRow_reshape, 1, cols); // inp, dir, num
  // console.log("inpZ: ", inpZ);
  // console.log("maxCurRow_reshape_clone:", maxCurRow_reshape_clone);
  var inpZ_shift = math.subtract(inpZ, maxCurRow_reshape_clone);
  // Z exp(z)
  //  expZ  sum
  var expZ = math.exp(inpZ_shift);
  var expZSum = math.sum(expZ, 1);
  var expZSum_reshape = math.reshape(expZSum, [expZSum.size()[0], 1]);
  var expZSumExtend = this.extendArr(expZSum_reshape, 1, cols);
  var outZ = math.dotDivide(expZ, expZSumExtend);
  return outZ;
};

/**
 * list 
 * 1. list;
 * 2. "A", "B",...
 */
NN.prototype.classOfAOnehot = function(aOnehot, classesStore) {
  var maxOfRow = Math.max(...aOnehot);
  var idxOfMax = aOnehot.indexOf(maxOfRow); // num
  var cl = classesStore[idxOfMax];
  return cl;
};

/**
 *  
 * @param X 
 * @param W
 * @param b 
 */
NN.prototype.calcProbInFP = function(curX, W, b) {
  // Z1 = X*W + b
  // var Z1 = math.add(math.multiply(X, W[1]), b[1]); // (n,4)
  // var A1 = this.sigmoid(Z1); // (n,4)

  // var Z2 = math.add(math.multiply(A1, W[2]), b[2]); // (n,3)
  // var A2 = this.softmax(Z2); // (n,3)
  // return A2;
  let Z = [];
  let A = [];
  let len_layerList = this.layerList.length;
  A[0] = curX;
  for (let j = 1; j < len_layerList; j++) {
    if (j < len_layerList - 1) {
      Z[j] = math.add(math.multiply(A[j - 1], W[j]), b[j]);
      A[j] = this.sigmoid(Z[j]);
    } else {
      Z[j] = math.add(math.multiply(A[j - 1], W[j]), b[j]);
      A[j] = this.softmax(Z[j]);
    }
  }
  return A[A.length - 1];
};

/**
 * NN
 * 
 * inputX: scaling
 * inputYonehot
 * @param layerList NN [ FeaturesL1L2]
 */
NN.prototype.modelTrainCV = function(
  layerList,
  stepSize,
  stepTotal,
  optimizer,
  lambda,
  isLogW,
  gxLen
) {
  this.layerList = layerList; //  , [numOfFeatures, m1,m2,..., numOfClass]
  // list bias
  var len_layerList = this.layerList.length; //  len - 2
  // var numOfFeatures = this.layerList[0];
  // var numOfClass = this.layerList[len_layerList - 1];

  this.stepSize = stepSize || 0.1;
  this.stepTotal = stepTotal || 100;
  this.optimizer = optimizer || "GD";
  this.lambda = lambda || 0; // 
  this.isLogW = isLogW || true;
  this.gxLen = gxLen || 50;
  //  RMSProp, Adadelta dw 

  var X = this.inputX; // 22Features
  var XT = math.transpose(X);
  var Y = this.inputY; // 33

  //  W,b
  var W = []; //  W w, [empty, [], [],...]
  var b = []; //  b b, [empty, [], [],...]
  var ZStore = []; //  Z1, Z2,...
  var AStore = []; //  A1, A2,...
  var ATStore = []; // A1T, A2T,...
  var dCdWStore = []; //  dCdW1, dCdW2, ...
  var dCdbStore = [];
  var dWStore = []; //  dW1, dW2, ...
  var dbStore = [];
  var deltaStore = []; // delta1, delta2,....
  var dCdWCur = []; // dCdW1, dCdW2,...
  var dCdbCur = [];
  this.ALarr = []; // NN 3cost
  for (let i = 1; i < len_layerList; i++) {
    //  0L0, L1, L2
    W[i] = math.random([layerList[i - 1], layerList[i]], -1, 1);
    b[i] = math.random([1, layerList[i]], -1, 1); // bnn

    //  Store arrarrpush
    dCdWStore[i] = [];
    dCdbStore[i] = [];
    dWStore[i] = [];
    dbStore[i] = [];
  }
  AStore[0] = X;
  ATStore[0] = XT;
  // 
  // var dCdW2Arr = []; //  dCdW gt.  dCdWStore[2]
  // var dCdb2Arr = []; // dCdbStore[2]
  // var dCdW1Arr = []; // dCdWStore[1]
  // var dCdb1Arr = []; // dCdbStore[1]

  // var dW2Arr = []; // dW2, dWStore[2]
  // var db2Arr = []; // dbStore[2]
  // var dW1Arr = []; // dWStore[1]
  // var db1Arr = []; // dbStore[1]
  for (let i = 0; i < this.stepTotal; i++) {
    // softmax
    // =================  ====================
    // layerList
    // 
    if (len_layerList > 2) {
      // FP 
      for (let j = 1; j < len_layerList; j++) {
        // softmax
        ZStore[j] = math.add(
          math.multiply(AStore[j - 1], W[j]),
          this.extendArr(math.matrix(b[j]), 0, AStore[j - 1].size()[0])
        ); // (n,4)

        if (j < len_layerList - 1) {
          // Z1 = X*W1+b1, A1 = sigmoid(Z1)
          AStore[j] = this.sigmoid(ZStore[j]); // (n,4)
        } else if (j == len_layerList - 1) {
          AStore[j] = this.softmax(ZStore[j]); // (n,4)
          this.ALarr.push(AStore[j]);
        }
      }
      // BP  
      for (let j = len_layerList - 1; j > 0; j--) {
        if (j == len_layerList - 1) {
          ATStore[j - 1] = math.transpose(AStore[j - 1]);
          deltaStore[j] = math.subtract(AStore[j], Y);
          dCdWCur[j] = math.add(
            math.multiply(ATStore[j - 1], deltaStore[j]),
            math.dotMultiply(this.lambda, W[j])
          );
          dCdbCur[j] = math.sum(deltaStore[j], 0);
          math.reshape(dCdbCur[j], [1, dCdbCur[j].size()[0]]);
        } else {
          ATStore[j - 1] = math.transpose(AStore[j - 1]);
          var delta1_left = math.multiply(
            deltaStore[j + 1],
            math.transpose(W[j + 1])
          ); // (n,3)(3,4)=(n,4)
          var sigmoid_Z1 = this.sigmoid(ZStore[j]); // (n,4)
          var delta1_right = math.dotMultiply(
            sigmoid_Z1,
            math.subtract(1, sigmoid_Z1)
          ); // (n,4)
          deltaStore[j] = math.dotMultiply(delta1_left, delta1_right); // (n,4)
          dCdWCur[j] = math.add(
            math.multiply(ATStore[j - 1], deltaStore[j]), // (2,n)(n,4)=(2,4)
            math.multiply(this.lambda, W[j]) // (2,4)
          ); // (2,4)
          dCdbCur[j] = math.sum(deltaStore[j], 0);
          math.reshape(dCdbCur[j], [1, dCdbCur[j].size()[0]]);
        }
      }
      // 
      if (this.optimizer == "GD" || this.optimizer == 0) {
        // console.log(" GD");
        for (let j = 1; j < len_layerList; j++) {
          W[j] = this.GradientDescent(W[j], this.stepSize, dCdWCur[j]);
          b[j] = this.GradientDescent(b[j], this.stepSize, dCdbCur[j]);
        }
      } else if (this.optimizer == "RMSProp" || this.optimizer == 1) {
        // console.log(" RMSProp new", i);
        for (let j = 1; j < len_layerList; j++) {
          W[j] = this.RMSProp_new(
            W[j],
            this.stepSize,
            dCdWCur[j],
            dCdWStore[j],
            this.gxLen
          );
          b[j] = this.RMSProp_new(
            b[j],
            this.stepSize,
            dCdbCur[j],
            dCdbStore[j],
            this.gxLen
          );
        }
      } else if (this.optimizer == "Adadelta" || this.optimizer == 2) {
        for (let j = 1; j < len_layerList; j++) {
          W[j] = this.Adadelta_new(
            W[j],
            this.stepSize,
            dCdWCur[j],
            dCdWStore[j],
            dWStore[j],
            this.gxLen
          );
          b[j] = this.Adadelta_new(
            b[j],
            this.stepSize,
            dCdbCur[j],
            dCdbStore[j],
            dbStore[j],
            this.gxLen
          );
        }
      }
    } else {
      // 
      // FP
      ZStore[1] = math.add(
        math.multiply(AStore[0], W[1]),
        this.extendArr(math.matrix(b[1]), 0, AStore[0].size()[0])
      ); // (n,4)

      AStore[1] = this.softmax(ZStore[1]); // (n,4)
      this.ALarr.push(AStore[1]);
      // BP
      ATStore[0] = math.transpose(AStore[0]);
      deltaStore[1] = math.subtract(AStore[1], Y);
      dCdWCur[1] = math.add(
        math.multiply(ATStore[0], deltaStore[1]),
        math.dotMultiply(this.lambda, W[1])
      );
      dCdbCur[1] = math.sum(deltaStore[1], 0);
      math.reshape(dCdbCur[1], [1, dCdbCur[1].size()[0]]);
      // update params
      if (this.optimizer == "GD" || this.optimizer == 0) {
        // console.log(" GD");
        for (let j = 1; j < len_layerList; j++) {
          W[j] = this.GradientDescent(W[j], this.stepSize, dCdWCur[j]);
          b[j] = this.GradientDescent(b[j], this.stepSize, dCdbCur[j]);
        }
      } else if (this.optimizer == "RMSProp" || this.optimizer == 1) {
        // console.log(" RMSProp new", i);
        for (let j = 1; j < len_layerList; j++) {
          W[j] = this.RMSProp_new(
            W[j],
            this.stepSize,
            dCdWCur[j],
            dCdWStore[j],
            this.gxLen
          );
          b[j] = this.RMSProp_new(
            b[j],
            this.stepSize,
            dCdbCur[j],
            dCdbStore[j],
            this.gxLen
          );
        }
      } else if (this.optimizer == "Adadelta" || this.optimizer == 2) {
        for (let j = 1; j < len_layerList; j++) {
          W[j] = this.Adadelta_new(
            W[j],
            this.stepSize,
            dCdWCur[j],
            dCdWStore[j],
            dWStore[j],
            this.gxLen
          );
          b[j] = this.Adadelta_new(
            b[j],
            this.stepSize,
            dCdbCur[j],
            dCdbStore[j],
            dbStore[j],
            this.gxLen
          );
        }
      }
    }
  }
  // console.log("A2:", A2);
  this.W = W;
  this.b = b;

  return this;
};
/**
 * softmax -log cost
 * @param ALarr  33
 * @param Y Labelsonehot
 * @returns  cost
 */
NN.prototype.calcCostArr = function(ALarr, Y) {
  // cost = -log(ALi)
  let costArr = [];
  // console.log("ALarr:", ALarr);
  // console.log("Y:", Y);
  ALarr.forEach(item => {
    // console.log("item.valueOf():", item.valueOf());
    let cost_mean = this.calcOneCost(item, Y);
    costArr.push(cost_mean);
  });

  return costArr;
};
NN.prototype.calcOneCost = function(AL, Y) {
  // lost = -y*log(h) - (1-y)*log(1-h)   1
  //  -log(ALi)
  // AL -log
  var minuslogAL = math.multiply(-1, math.log(AL));
  var minuslogALy = math.dotMultiply(Y, minuslogAL);
  var cost_mean = math.mean(minuslogALy);

  // let hy = math.dotMultiply(AL, Y);
  // console.log("hy:", hy);
  // let cost = math.dotMultiply(-1, math.log(hy));
  // let cost_mean = math.mean(cost);
  return cost_mean;
};
